import React, { useMemo, useState } from "react";
import SubjectIcon from "@mui/icons-material/Subject";
import FilterListIcon from "@mui/icons-material/FilterList";
import SendIcon from "@mui/icons-material/Send";
import { AppBar, Box, Button, Toolbar, useMediaQuery } from "@mui/material";
import { EmailTemplateDrawer } from "../../components/email-template-drawer/EmailTemplateDrawer";
import { ContactList } from "../../components/contact-list/ContactList";
import { FilterDrawer } from "../../components/filter-drawer/FilterDrawer";
import { useContacts } from "../../common/contacts-context";
import { useEmailTemplate } from "../../common/email-template-context";
import { useExclusionList } from "../../common/exclusion-context";

function Home() {
  const isWideScreen = useMediaQuery("(min-width:932px)");
  const [drawer, setDrawer] = useState();

  const [{ subject, body }] = useEmailTemplate();

  const contacts = useContacts();
  const [excluded, setExcluded] = useExclusionList();
  const contactsToSend = useMemo(() => {
    return contacts.filter((contact) => !excluded.includes(contact.id));
  }, [contacts, excluded]);

  return (
    <div>
      <AppBar>
        <Toolbar>
          <Box
            sx={{
              display: "flex",
              maxWidth: 900,
              width: "100%",
              ml: "auto",
              mr: "auto",
            }}
          >
            <Button
              variant="outlined"
              color="inherit"
              startIcon={<SubjectIcon />}
              onClick={() => setDrawer("email-drawer")}
            >
              Template
            </Button>
            <Box component="div" sx={{ flexGrow: 1 }} />
            <Button
              variant="outlined"
              color="inherit"
              endIcon={<FilterListIcon />}
              onClick={() => setDrawer("filter-drawer")}
            >
              Filter
            </Button>
          </Box>
        </Toolbar>
        <Toolbar sx={{ backgroundColor: "white", color: "black" }}>
          <Box
            sx={{
              display: "flex",
              maxWidth: 900,
              width: "100%",
              ml: "auto",
              mr: "auto",
            }}
          >
            <Button
              variant="outlined"
              startIcon={<SendIcon />}
              disabled={!contactsToSend.length}
              onClick={(event) => {
                globalThis.open(
                  `mailto:${contactsToSend
                    .map((c) => c.email)
                    .join(
                      window.navigator.platform.startsWith("Win") ? ";" : ","
                    )}?subject=${subject}&body=${body}`,
                  "mail"
                );
                event.preventDefault();
                // if (!isExcluded) {
                //   setExcluded([...excluded, contact.id]);
                // }
              }}
            >
              Send bulk to {contactsToSend.length}{" "}
              {contactsToSend.length === 1 ? "member" : "members"}
            </Button>
          </Box>
        </Toolbar>
      </AppBar>
      <Toolbar />
      <Toolbar />
      <Box
        sx={{
          my: 2,
          maxWidth: 900,
          ...(isWideScreen
            ? {
                mx: "auto",
                width: "100%",
              }
            : {
                mx: 2,
                width: "calc(100% - 32px)",
              }),
        }}
      >
        <EmailTemplateDrawer
          open={drawer === "email-drawer"}
          onClose={() => setDrawer()}
        />
        <FilterDrawer
          open={drawer === "filter-drawer"}
          onClose={() => setDrawer()}
        />
        <ContactList />
      </Box>
    </div>
  );
}

export default Home;
